<template>
    <div class="app">
      <div class="app_top">
        <img class="app_bk_mask" src="../assets/img/rp_top_mask1.png" alt="">
        <div class="app_textarea">
          <textarea v-model="redStr" :placeholder="$t('RedPacketCode')"></textarea>
        </div>
        <div class="app_btn" @click="goNext">GO</div>
        <img class="app_logo" src="../assets/img/logo.png" alt="" srcset="">
      </div>
      <div class="app_title">{{$t('ENU Red Envelope')}}</div>
      <div class="app_bottom">
        <div class="app_lang" @click="checkLang">EN/中文</div>
        <div class="app_aciton">
          <span><router-link to="/red">{{$t('Create Red Envelope')}}</router-link></span>
          <span class="app_tip"> | </span>
          <span><router-link to="/account">{{$t('Create ENU Account')}}</router-link></span>
          <span class="app_tip"> | </span>
          <span><router-link to="/about">{{$t('About Us')}}</router-link></span>
        </div>
      </div>
      <div class="app_down">
        <img src="../assets/img/down.png" alt="">
      </div>
      <div class="app_footer">
        <p><span class="Line"></span><span class="Text">{{$t('Cooperation Institutions')}}</span><span class="Line"></span></p>
        <img src="../assets/img/footer_mask_img.png" alt="">
      </div>
    <loading :loading-status="loading"></loading>
    <message :message-status="messageStatus" :message-text="messageText"></message>
    </div>
</template>

<script>
export default {
  components: {
    loading: resolve => {
      require(["@/components/loading.vue"], resolve);
    },
    message: resolve => {
      require(["@/components/message.vue"], resolve);
    }
  },
  data() {
    return {
      loading: false,
      messageStatus: false,
      messageText: "",
      redStr: ""
    };
  },
  created() {
    this.lang = localStorage.getItem("Lang");
  },
  methods: {
    // 点击GO
    goNext() {
      if (this.redStr == "") {
        this.$message.error(this.$t("EnterRedMessage"));
      } else {
        let id = this.redStr.split("=")[1].split("&")[0];
        let f = this.redStr.split("=")[2].split("&")[0];
        let l = this.redStr.split("=")[3].split("&")[0];
        let lang = this.redStr.split("=")[4].split(/\n/)[0];
        this.$router.push({
          path: "receive?id=" + id + "&f=" + f + "&l=" + l + "&lang=" + lang
        });
      }
    },
    showMessage(message) {
      let _this = this;
      _this.messageStatus = true;
      _this.messageText = message;
      setTimeout(function() {
        _this.messageStatus = false;
        _this.messageText = "";
      }, 1500);
    },
    checkLang() {
      if (this.lang == "en") {
        localStorage.setItem("Lang", "zh");
        this.$i18n.locale = "zh";
        this.lang = "zh";
      } else {
        localStorage.setItem("Lang", "en");
        this.$i18n.locale = "en";
        this.lang = "en";
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@rem: round(1/75 * 2, 6) * 1rem;

.app {
  width: 100%;
  height: 100%;
  background: #d62748;
  position: relative;
  .app_top {
    width: 100%;
    box-sizing: border-box;
    position: relative;
    height: 326 * @rem;
    z-index: 2;
    background: #ffffff;
    .app_bk_mask {
      width: 100%;
    }
    .app_textarea {
      position: absolute;
      top: 10%;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      height: 36%;
      text-align: center;
      textarea {
        border: 1 * @rem solid #dedede;
        border-radius: 2 * @rem;
        height: 100%;
        padding: 10 * @rem;
        width: 80%;
        resize: none;
        color: #5d4220;
        font-size: 12 * @rem;
        outline: none;
      }
    }
    .app_btn {
      width: 126 * @rem;
      height: 38 * @rem;
      line-height: 38 * @rem;
      background-color: #dd2436;
      border: 0 none;
      border-radius: 19 * @rem;
      font-size: 16 * @rem;
      color: #fff7c1;
      position: absolute;
      left: 50%;
      bottom: 28%;
      transform: translateX(-50%);
      text-align: center;
    }
    .app_logo {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      width: 52 * @rem;
      height: 52 * @rem;
      bottom: 5%;
    }
  }
  .app_title {
    margin-top: 40 * @rem;
    text-align: center;
    font-size: 24 * @rem;
    color: #fff3d1;
  }
  .app_bottom {
    position: absolute;
    bottom: 46 * @rem;
    width: 100%;
    max-width: 375 * @rem;
    z-index: 2;
    margin-top: 30 * @rem;
    color: #fcdbb2;
    font-size: 12 * @rem;
    opacity: 0.7;
    .app_lang {
      width: 56 * @rem;
      height: 20 * @rem;
      line-height: 20 * @rem;
      background: rgba(0, 0, 0, 0.09);
      color: #eba08e;
      border-radius: 10 * @rem;
      margin-top: 40 * @rem;
      font-size: 12 * @rem;
      text-align: center;
      margin-left: 50%;
      transform: translateX(-50%);
    }
    .app_aciton {
      position: relative;
      z-index: 2;
      margin-top: 30 * @rem;
      color: #fcdbb2;
      font-size: 12 * @rem;
      opacity: 0.7;
      text-align: center;
      span {
        a {
          text-decoration: none;
          color: #fcdbb2;
          font-size: 12 * @rem;
        }
      }
      .app_tip {
        margin: 0 10 * @rem;
      }
    }
  }
  .app_down {
    position: absolute;
    bottom: 5 * @rem;
    display: flex;
    justify-content: center;
    left: 50%;
    transform: translateX(-50%);
    img {
      width: 20 * @rem;
      height: 20 * @rem;
      animation-name: myfirst;
      animation-duration: 1.5s;
      animation-timing-function: linear;
      animation-iteration-count: infinite;
    }

    @keyframes myfirst {
      0% {
        transform: rotate(180deg) translateY(0);
      }
      50% {
        transform: rotate(180deg) translateY(5px);
      }
      100% {
        transform: rotate(180deg) translateY(0);
      }
    }
  }
  .app_footer {
    position: absolute;
    top: 100%;
    max-width: 375 * @rem;
    min-height: 300 * @rem;
    background: #d62748;
    p {
      margin-top: 20 * @rem;
      width: 100%;
      height: 50 * @rem;
      padding: 0 40 * @rem;
      box-sizing: border-box;
      display: flex;
      align-items: center;
      justify-content: space-around;
      .Line {
        width: 50 * @rem;
        height: 2 * @rem;
        background: #fff7c1;
      }
      .Text {
        font-size: 14 * @rem;
        color: #fff7c1;
        text-align: center;
      }
    }
    img {
      width: 100%;
    }
  }
}
</style>



